<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido(data-aos="flip-up")
      .titulo-principal__numero
        span 5
      h1 Construccin a partir del frontal
    
    p.mb-5 El proceso de patronaje comienza con el <strong>molde frontal</strong>, llamado molde patrn. Este molde principal sirve como punto de partida para definir las dimensiones y proporciones del resto de las piezas del bolso.
    
    <!-- BLOQUE 1 -->
    #t_5_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 5.1 Elaboracin del molde patrn con ejes de simetra

    p.mb-5 Las dimensiones indicadas a continuacin son orientativas y pueden adaptarse segn las necesidades funcionales o creativas del diseo. El modelo propuesto puede presentar variaciones en funcin de los requerimientos especficos del proyecto.

    TabsC.color-acento-contenido.mb-5(data-aos="zoom-in")
      .py-3.py-md-4(titulo="Preparacin del material")
        .row.justify-content-center.align-items-center
          .col-xl-5.order-2.order-lg-1
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img32.png', alt="").m-auto
          .col-xl-7.order-1.order-lg-2.mb-4.mb-lg-0
            h4.txt--purpura.mb-4 Preparacin del material
            p.mb-0 Recorta una cartulina o papel rgido en un rectngulo de <strong>44 cm x 34 cm.</strong> Este ser el soporte para el molde patrn.

      .py-3.py-md-4(titulo="Trazado de los ejes de simetra")
        .row.justify-content-center.align-items-center
          .col-xl-5.order-2.order-lg-1
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img33.png', alt="" ).m-auto
          .col-xl-7.order-1.order-lg-2.mb-4.mb-lg-0
            h4.txt--purpura.mb-4 Trazado de los ejes de simetra
            h5.mb-3 Eje vertical:
            <ul class="fa-ul estilo___ul mb-4">
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Coloca la cartulina en posicin horizontal <strong>(44 cm de ancho x 34 cm de alto)</strong>.</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Marca el punto medio del borde superior (a los <strong>22 cm</strong>) y repite en el borde inferior.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Une ambos puntos con la regla y, con la cuchilla, traza suavemente la lnea para obtener el eje vertical.</li>
            </ul>
            h5.mb-3 Eje horizontal:
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Dobla la cartulina por el eje vertical.</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Marca el punto medio del borde lateral (a los <strong>17 cm</strong>) con el punzn para que quede reflejado en ambos lados.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Desdobla y une las marcas con la regla y la cuchilla, obteniendo as el eje horizontal.</li>
            </ul>
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Aplicaciones y recomendaciones
    
    p.mb-5 Los ejes de simetra actan como referencias centrales para <strong>distribuir de forma equilibrada todos los elementos del molde</strong>, asegurando proporciones correctas y facilitando la construccin de piezas simtricas. Si se requieren medidas distintas para otros modelos, aplica el mismo procedimiento para recalcular los puntos centrales, manteniendo la precisin y la simetra en cualquier formato.

    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 15.
          span Cartulina con ejes de simetra
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img34.png', alt='Fotografa de cartulina rectangular colocada horizontalmente con ejes vertical y horizontal trazados, lista para el diseo del patrn frontal del bolso Messenger.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Trazado del contorno frontal a partir de los ejes de simetra
    
    p.mb-5 Con los ejes ya definidos, se procede a ubicar los puntos clave y formar el contorno del frontal.

    AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")(data-aos="fade-left").mb-5
      .row(titulo="Ubicacin del primer punto de referencia").ajuste-cajaAcordion
        p.mb-3 Sobre el eje vertical, mide 20 cm hacia la derecha desde el centro. Desde el eje horizontal, mide 15 cm hacia arriba y marca. La interseccin de ambas medidas define el primer punto en el cuadrante superior derecho. 
        .row.justify-content-center.mb-3
          .col-xl-6
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 16.
              span Primer punto para construccin del frontal
            figure
              img(src='@/assets/curso/temas/img35.png', alt='Ilustracin que indica el primer punto de referencia para la construccin del frontal, a partir de mediciones desde los ejes de simetra.').m-auto
      .row(titulo="Transferencia precisa por simetra").ajuste-cajaAcordion
        p.mb-3  Dobla la cartulina por el eje vertical y, con un punzn, transfiere el punto al lado izquierdo, asegurando que quede perfectamente simtrico. Al desdoblar la cartulina, se obtienen dos puntos superiores idnticos, cada uno a 15 cm del eje horizontal y 20 cm desde el centro hacia los laterales. 
        .row.justify-content-center.mb-3
          .col-xl-6.mb-3
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 17.
              span Segundo punto
            figure
              img(src='@/assets/curso/temas/img36.png', alt='Imagen del segundo punto de referencia ubicado de forma simtrica en el patrn frontal.').m-auto
      .row(titulo="Marcado de los puntos inferiores").ajuste-cajaAcordion
        p.mb-3 Dobla la cartulina por el eje horizontal y, con el punzn, transfiere ambos puntos superiores a la parte inferior. Al desdoblar, se obtienen cuatro puntos distribuidos de manera simtrica respecto a los dos ejes. 
        .row.justify-content-center.mb-3
          .col-xl-6.mb-3
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 18.
              span Tercer y cuarto punto
            figure
              img(src='@/assets/curso/temas/img37.png', alt='Imagen de los puntos inferior derecho e izquierdo localizados en el patrn, equidistantes desde los ejes para asegurar simetra.').m-auto
      .row(titulo="Delineado del contorno").ajuste-cajaAcordion
        p.mb-3 Une los cuatro puntos con lneas rectas o curvas suaves para definir el contorno general del frontal.
        .row.justify-content-center.mb-3
          .col-xl-6.mb-3
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 19.
              span Contorno del frontal
            figure
              img(src='@/assets/curso/temas/img38.png', alt='Ilustracin del contorno del frontal del bolso con lneas rectas y curvas suaves conectando los puntos de referencia.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Definicin y trazado de las curvas inferiores del frontal
    
    p.mb-5 Una vez definido el contorno, se ajustan las esquinas para dar una transicin suave entre laterales y base.

    AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")(data-aos="fade-left").mb-5
      .row(titulo="A. Marcacin de los puntos gua").ajuste-cajaAcordion
        p.mb-3 Ubcate en el punto inferior derecho del contorno. Con el comps ajustado a un radio de 2,5 cm, colcalo en el vrtice y dibuja dos pequeos arcos: uno hacia la lnea vertical (lateral) y otro hacia la lnea horizontal (base). Marca ambos puntos de interseccin, que servirn como guas para la curva.
        .row.justify-content-center.mb-3
          .col-xl-6.mb-3
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 20.
              span Puntos para construccin de la curva
            figure
              img(src='@/assets/curso/temas/img39.png', alt='Ilustracin que indica el primer punto de referencia para la construccin del frontal, a partir de mediciones desde los ejes de simetra.').m-auto
      .row(titulo="B. Determinacin del centro y trazado de la curva").ajuste-cajaAcordion
        p.mb-3 Coloca el comps en cada punto gua y dibuja arcos que se crucen en el interior del patrn. El punto de cruce ser el centro geomtrico de la curva. Desde este centro, traza la curva definitiva que una de forma suave y simtrica el lateral con la base. Repite el proceso en la esquina inferior izquierda para mantener la simetra.
        .row.justify-content-center
          .col-xl-10
            .row.align-items-center.mb-3
              .col-xl-6
                .titulo-sexto.color-acento-contenido.mb-2
                  h5 Figura 21.
                  span Mediatriz
                figure
                  img(src='@/assets/curso/temas/img40.png', alt='Ilustracin que muestra la mediatriz y la ubicacin del centro geomtrico de la curva a trazar en la base del frontal.').m-auto
              .col-xl-6.mb-3.espacio___movil_3
                .titulo-sexto.color-acento-contenido.mb-2
                  h5 Figura 22.
                  span Curva del frontal
                figure
                  img(src='@/assets/curso/temas/img41.png', alt='Imagen de curva inferior definida y trazada con comps en el patrn frontal del bolso.').m-auto
      .row(titulo="C. Simetra de la curva al lateral opuesto").ajuste-cajaAcordion
        p.mb-3 Dobla el molde patrn cuidadosamente por el eje vertical, asegurndote de que ambos ejes coincidan. Con una carretilla de modistera, un punzn o mediante una ligera incisin con cuchilla, transfiere el contorno de la curva recin trazada hacia el lado contrario. Este paso garantiza que la curvatura sea idntica en ambos extremos inferiores del frontal. 
        .row.justify-content-center.mb-3
          .col-xl-6.mb-3
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 23.
              span Simetra de la curva
            figure
              img(src='@/assets/curso/temas/img42.png', alt='Ilustracin del proceso de doblado y transferencia de la curva a travs del eje vertical para obtener simetra en ambos lados.').m-auto
      .row(titulo="D.	Replicacin de la curvatura en el sector superior (si aplica)").ajuste-cajaAcordion
        p.mb-3 Si el diseo del bolso requiere curvas en las esquinas superiores, repite el mismo procedimiento tomando como referencia los puntos superiores del patrn. Realiza la simetra cruzando el eje horizontal para asegurar que las cuatro esquinas presenten curvaturas homogneas y proporcionales.
    
    p.mb-5 El patrn frontal queda con las curvas definidas y perfectamente simtricas, ya sea en dos o en las cuatro esquinas, segn el diseo. Esto garantiza precisin en el patronaje y facilita un acabado uniforme y profesional en la confeccin.

    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Dibujo de la tapa

    .row.align-items-center.m-0.mb-5.style___box_9(data-aos="zoom-in")
      .col-xl.style___box_10
        p.mb-0 La tapa se traza tomando como referencia la parte superior del cuerpo frontal. Su diseo puede ser recto, curvo o con ngulos, pero siempre debe cubrir por completo la boca del bolso y permitir un cierre seguro. Tambin se deben marcar los puntos de sujecin a la espalda y el sistema de cierre elegido (broche, velcro, hebilla, etc.).
      .col-xl-auto
        figure.d-none.d-xl-block
          img(src='@/assets/curso/temas/img43.png', alt='').m-auto
    
    .row.align-items-center.mb-5
      .col-xl
        p.mb-3 Para el modelo propuesto, se dibuja primero una lnea horizontal a <strong>22 cm</strong> del borde superior del frontal. A partir de esta lnea se construye la forma curva de la tapa, empleando un radio de <strong>2,5 cm</strong> en las esquinas, siguiendo el mismo procedimiento utilizado para las curvas de la base. Esto proporciona un acabado esttico y funcional que facilita el cierre del bolso.
        .row.align-items-center.mb-3
          .col-xl-auto(data-aos="fade-right")
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img44.svg', alt='').m-auto
          .col-xl
            p.mb-0 Para definir la parte posterior de la tapa, la seccin que se conecta con el espaldar, se dibuja una lnea horizontal a <strong>3 cm</strong> del borde superior del patrn, paralela a dicho borde.
        p.mb-0 Sobre esta lnea, tomando como referencia el borde vertical del patrn, se mide <strong>3 cm</strong> hacia el interior y se marca un punto. Desde la esquina superior del frontal se traza una lnea diagonal hasta este punto, estableciendo el ngulo de insercin de la tapa en el espaldar.
      .col-xl-auto.espacio___movil_4
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 24.
          span Trazado de la tapa
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img45.png', alt='Imagen del trazado de la tapa del bolso sobre el patrn frontal, utilizando curvas y lneas de referencia para su forma.').m-auto
    
    p.mb-5 Una vez definida la diagonal, se realiza el proceso de simetra para trasladarla al lado opuesto, asegurando una forma equilibrada y proporcional. Con esto se completa el diseo de la tapa, garantizando tanto su funcionalidad como su armona esttica.

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center
          .col-xl-auto(data-aos="fade-right")
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img46.png', alt='').m-auto
          .col-xl
            .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
              h5 Figura 25.
              span Parte posterior de la tapa
            figure(data-aos="zoom-in")
              img(src='@/assets/curso/temas/img47.png', alt='Ilustracin de la parte posterior de la tapa, con lneas y medidas para su unin al espaldar del bolso.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Dibujar las correas
    
    p.mb-5 Con la tapa finalizada, el siguiente paso es marcar la ubicacin y forma de las correas que servirn para el sistema de cierre. Para este modelo se utilizar una hebilla de <strong>1 pulgada</strong>, por lo que el ancho de la correa debe ser ligeramente inferior, fijndose en <strong>2,4 cm</strong>. Esta medida puede variar segn el herraje seleccionado; por eso es imprescindible contar con todos los herrajes antes de iniciar el modelado para verificar sus dimensiones reales. El trazado se realiza en cuatro pasos:

    .row.justify-content-center.mb-5
      .col-xl-10
        TabsA.color-acento-botones(data-aos="zoom-in")
          .tarjeta.color-acento-botones--borde.p-4(titulo="Definicin del ancho de la correa")
            h4 Definicin del ancho de la correa
            p.mb-0 Desde el eje vertical de simetra del patrn de la tapa, traza una lnea paralela a 12 cm hacia un lado. Luego, dibuja una segunda lnea paralela a 2,4 cm de la anterior; la distancia entre ambas determina el ancho de la correa.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Definicin del largo de la correa")
            h4 Definicin del largo de la correa
            p.mb-0 Traza dos lneas horizontales paralelas a la base inferior de la tapa: una a 5 cm hacia arriba y otra a 7 cm hacia abajo. Este segmento establece la longitud visible de la correa en el patrn.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Curvas inferiores")
            h4 Curvas inferiores
            p.mb-0 Para un acabado ms estilizado, redondea suavemente las esquinas inferiores con un radio de 1 cm, evitando ngulos rgidos.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Limpieza y simetra")
            h4 Limpieza y simetra
            p.mb-0 Una vez definida la primera correa, elimina las lneas de construccin sobrantes. Luego, aplica el proceso de simetra respecto al eje vertical para trasladar la correa al lado opuesto de la tapa, garantizando una ubicacin equilibrada y esttica.
    
    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 26.
          span Trazado correas tapa
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img48.png', alt='Imagen de correas dibujadas sobre la tapa del patrn, con lneas paralelas que definen ancho y largo, adems de curvas redondeadas en extremos. ').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Dibujar el bolsillo frontal
    
    p.mb-5 Con las correas ya definidas, se pasa al diseo del <strong>bolsillo frontal</strong>, elemento que aporta funcionalidad y esttica, permitiendo guardar objetos pequeos y de acceso rpido. El trazado se desarrolla en los siguientes pasos:

    .row.justify-content-center.mb-5
      .col-xl-10
        TabsA.color-acento-botones(data-aos="zoom-in")
          .tarjeta.color-acento-botones--borde.p-4(titulo="Definicin del ancho del bolsillo")
            h4 Definicin del ancho del bolsillo
            p.mb-0 Desde el eje vertical de simetra del cuerpo frontal, traza dos lneas paralelas a 18 cm hacia cada lado. Este trazado genera un ancho total de 36 cm para el bolsillo, equilibrado respecto al centro del frontal.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Determinacin del alto del bolsillo")
            h4 Determinacin del alto del bolsillo
            p.mb-0 Tomando como referencia el eje horizontal del patrn, traza una lnea a 10 cm hacia arriba y otra a 12 cm hacia abajo. Estas lneas definen la altura total del bolsillo.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Curvatura inferior")
            h4 Curvatura inferior
            p.mb-0 En cada esquina inferior del bolsillo, dibuja una curva con radio de 2,5 cm, siguiendo el mismo procedimiento aplicado a las esquinas del frontal y de la tapa. Esta curva suaviza el diseo, mejora la esttica y facilita el montaje.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Terminacin superior")
            h4 Terminacin superior
            p.mb-0 La parte superior se mantiene en ngulo recto, proporcionando un borde limpio y firme que facilita la confeccin e instalacin de cierres, vistas o remates.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Limpieza final")
            h4 Limpieza final
            p.mb-0 Una vez definidos todos los lmites, elimina las lneas de construccin sobrantes para dejar el contorno definitivo del bolsillo, listo para su corte y confeccin.
    
    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 27.
          span Trazado bolsillo frontal
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img49.png', alt='Ilustracin del bolsillo frontal sobre el patrn, con dimensiones y curvaturas indicadas para su confeccin.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Dibujar hebillero en bolsillo frontal
    
    p.mb-5 Con el bolsillo frontal definido, se marca la ubicacin del <strong>hebillero</strong>, pieza clave para el cierre y ajuste del bolso.

    .fondo___slider.mb-5
      .px-5
        .ajuste-slider(data-aos="zoom-in")
          SlyderA(tipo="b")
            .row.h-100.justify-content-center.align-items-center
              .col-xl-6.mb-4
                h4 Ubicacin sobre la correa
                p.mb-0 En cada correa dibujada previamente en la tapa, traza dos lneas paralelas al borde inferior: la primera a 1,5 cm del borde y la segunda a 4 cm del mismo borde. Este espacio delimita la zona donde se fijar el hebillero.
              .col-xl-6
                figure.d-none.d-xl-block
                  img(src='@/assets/curso/temas/img51.png', alt='').m-auto
            
            .row.h-100.justify-content-center.align-items-center
              .col-xl-6.mb-4
                h4 Simetra
                p.mb-0 Repite el procedimiento en la correa del lado opuesto, utilizando el eje vertical de simetra como referencia para garantizar una ubicacin exacta y un diseo equilibrado.
              .col-xl-6
                figure.d-none.d-xl-block
                  img(src='@/assets/curso/temas/img52.png', alt='' ).m-auto

            .row.h-100.justify-content-center.align-items-center
              .col-xl-6.mb-4
                h4 Consideraciones tcnicas
                p.mb-0 Las medidas deben adaptarse al tipo de hebilla seleccionada. Es recomendable contar con los herrajes fsicos al momento del trazado para comprobar proporciones y asegurar la correcta insercin y fijacin del hebillero durante la confeccin.
              .col-xl-6
                figure.d-none.d-xl-block
                  img(src='@/assets/curso/temas/img53.png', alt='' ).m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Dibujar el bolsillo de la tapa
    
    p.mb-5 Finalmente, se incorpora el <strong>bolsillo superior de la tapa</strong>, diseado como compartimento funcional para guardar objetos de uso frecuente. Puede llevar cremallera visible o cubierta, segn el estilo del bolso y las preferencias del usuario.

    .row.justify-content-center.mb-5
      .col-xl-10
        TabsA.color-acento-botones(data-aos="zoom-in")
          .tarjeta.color-acento-botones--borde.p-4(titulo="Ubicacin del bolsillo")
            h4 Ubicacin del bolsillo
            p.mb-0 Desde la lnea superior del frontal (ya establecida como referencia en la tapa), traza dos lneas horizontales paralelas: la primera a 4 cm hacia abajo desde el borde superior y la segunda a 1,4 cm debajo de la anterior. Este espacio define el alto del canal donde se instalar la cremallera. En este ejemplo se usa una cremallera nmero 6; si el ancho es distinto, se ajustar la medida de acuerdo con el herraje.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Delimitacin lateral del bolsillo")
            h4 Delimitacin lateral del bolsillo
            p.mb-0 Desde el borde vertical de la tapa, traza una lnea paralela a 3 cm hacia el interior, indicando el inicio del terminal de la cremallera. Con el eje vertical de simetra, refleja esta medida hacia el lado opuesto para garantizar que la cremallera quede perfectamente centrada.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Recomendaciones tcnicas")
            h4 Recomendaciones tcnicas
            p.mb-0 Es indispensable contar con la cremallera y sus componentes fsicos durante el trazado para verificar que las dimensiones se ajusten al modelo elegido. Esto asegura un funcionamiento ptimo y acabados precisos en la confeccin final.
    
    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 28.
          span Trazado bolsillo tapa
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img54.png', alt='Imagen que presenta el diseo del bolsillo superior con cremallera sobre la tapa del patrn, mostrando ubicacin y medidas. ').m-auto
    
    .row.justify-content-center.mb-5.m-0
      .col-xl-10.style___box_11(data-aos="zoom-in")
        p.mb-0 Con este paso, se completa la definicin del molde patrn, dejando establecidas todas las piezas y elementos necesarios para el desarrollo del bolso tipo <em>Messenger</em>.
    
    .row.justify-content-center
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 29.
          span Molde patrn definido
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img55.png', alt='Fotografa del molde patrn final del bolso tipo Messenger, con todas las piezas y elementos dibujados.').m-auto
        
    <!-- BLOQUE 2 -->
    Separador
    #t_5_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 5.2 Despieces de los diferentes moldes
    
    p.mb-5 Con el molde patrn definido y las piezas principales dibujadas, se procede al <strong>despiece</strong> de cada uno de los componentes que conforman el bolso <em>Messenger</em>. Este proceso consiste en individualizar cada parte para su posterior trazado, corte y confeccin, comenzando por la <strong>base del bolso</strong>.

     .row.justify-content-center.mb-5
      .col-xl-10
        TabsA.color-acento-botones(data-aos="zoom-in")
          .tarjeta.color-acento-botones--borde.p-4(titulo="Obtencin de medidas")
            h4 Obtencin de medidas
            p.mb-0 Con una regla o cinta mtrica, mide el permetro del patrn frontal, iniciando desde el punto donde comienzan las curvas inferiores. Esta medida corresponde al largo de la base.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Determinacin del ancho")
            h4 Determinacin del ancho
            p.mb-0 Define el ancho de acuerdo con la capacidad deseada del bolso. En este ejemplo se asigna un ancho de 10 cm, ajustable segn necesidades funcionales o estticas del diseo.
          .tarjeta.color-acento-botones--borde.p-4(titulo="Asignacin de mrgenes de costura")
            h4 Asignacin de mrgenes de costura
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>En la lnea de unin entre la base y las piezas del frontal y espaldar, deja un aumento de 8 mm para garantizar un ensamblaje preciso.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>En las lneas de unin con los fuelles o laterales, asigna un aumento de 7 mm destinados al doblado, lo que facilita un acabado limpio.</li>
            </ul>
          .tarjeta.color-acento-botones--borde.p-4(titulo="Adaptaciones posibles")
            h4 Adaptaciones posibles
            p.mb-0 Los mrgenes pueden modificarse segn el tipo de material y el mtodo de ensamblaje utilizado.
    
    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 30.
          span Patrn de la base o fondo
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img56.png', alt='Ilustracin del patrn de la base o fondo del bolso Messenger, con forma rectangular, medidas indicadas y aumentos de costura marcados en los bordes.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn fuelle
    
    p.mb-5 Con la base lista, se procede a elaborar el molde del <strong>fuelle</strong>, pieza que conforma los laterales del bolso y que une el frontal con el espaldar. Para obtenerlo, se mide el recorrido del frontal desde el nacimiento de la curva inferior hasta la parte superior; esta medida define el largo del fuelle.

    .row.justify-content-center(data-aos="zoom-in")
      .col-xl-10
        .row.align-items-center.m-0.mb-5.style___box_7.ajuste___box_7
          .col-xl-auto
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img57.png', alt='').m-auto
          .col-xl
            p.mb-0 El ancho estndar se establece en <strong>10 cm</strong>, igual al de la base. Sin embargo, si el diseo busca una silueta ms estilizada, el fuelle puede adoptar forma trapezoidal: en ese caso, se mantiene el ancho de 10 cm en la base y se reduce a <strong>8 cm</strong> en la parte superior o boca del bolso.

        p.mb-5 Una vez definida la forma, se asignan los mrgenes de costura:
    
    .row.mb-5
      .col-xl-4(data-aos="zoom-in")
        .style___box_5.h-100
          .row.justify-content-center.mb-3
            .col-7
              figure.mb-4.d-none.d-xl-block(data-aos="fade-right")
                img(src='@/assets/curso/temas/img58.svg', alt="" style="max-width: 100px").m-auto
            p.mb-0 8 mm en los laterales que se unirn con el frontal y el espaldar.
      .col-xl-4(data-aos="zoom-in").espacio___movil_3
        .style___box_5.h-100
          .row.justify-content-center.mb-3
            .col-7
              figure.mb-4.d-none.d-xl-block(data-aos="fade-right")
                img(src='@/assets/curso/temas/img59.svg', alt="" style="max-width: 100px").m-auto
            p.mb-0 1 cm en el borde inferior para la unin con la base.
      .col-xl-4(data-aos="zoom-in").espacio___movil_3
        .style___box_5.h-100
          .row.justify-content-center.mb-3
            .col-7
              figure.mb-4.d-none.d-xl-block(data-aos="fade-left")
                img(src='@/assets/curso/temas/img60.svg', alt="" style="max-width: 100px").m-auto
            p.mb-0 7 mm en la parte superior, destinados al doblado o a una terminacin limpia en la boca del bolso.
    
    .row.justify-content-center.mb-5
      .col-xl-10
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 31.
          span Patrn del fuelle
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img61.png', alt='Imagen del patrn del fuelle del bolso, pieza lateral con dimensiones rectangulares y aumentos para costura y doblado, diseada para aportar profundidad y capacidad al bolso Messenger.').m-auto
    
    .row.justify-content-center.mb-4
      .col-xl-10
        .style___box_11.mb-4.ajuste___box_11(data-aos="zoom-in")
          p.mb-0 Adems de definir la forma del fuelle, es fundamental marcar la ubicacin de la <strong>chapeta</strong> que sostendr el cuadrante para la correa. En este modelo se emplear un cuadrante de <strong>1 pulgadas</strong> (aproximadamente 3 cm), por lo que la chapeta tendr un ancho de <strong>2,8 cm</strong> y un largo de <strong>4 cm</strong> para permitir un ajuste seguro y esttico. Para dibujar la chapeta en el molde del fuelle:

        p.mb-0 Traza el <strong>eje vertical</strong> en el centro del molde.
    
    .row.justify-content-center.mb-5.m-0.style___box_11.ajuste___box_11_1
      .col-xl-10
        .row.align-items-center
          .col-xl-7
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-3"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Desde el borde superior del fuelle (sin incluir el aumento para doblado), dibuja una lnea paralela a <strong>3 cm</strong> hacia abajo.</li>
              <li class="mb-3"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Desde esa lnea, marca otra paralela a <strong>4 cm</strong> ms abajo; este tramo define el largo de la chapeta.</li>
              <li class="mb-3"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>A partir del eje vertical, mide <strong>1,4 cm</strong> hacia cada lado y dibuja lneas paralelas para obtener el ancho de <strong>2,8 cm</strong>.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Borra las lneas de construccin sobrantes, dejando visible nicamente el rectngulo final que representa la chapeta.</li>
            </ul>
          .col-xl-5.espacio___movil_4
            .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
              h5 Figura 32.
              span Trazado chapeta en fuelle
            figure(data-aos="zoom-in")
              img(src='@/assets/curso/temas/img62.png', alt='Imagen del patrn del fuelle del bolso, pieza lateral con dimensiones rectangulares y aumentos para costura y dobladoIlustracin del trazado de chapeta sobre el molde del fuelle, con medidas precisas para sostener el cuadrante de la correa, incluyendo ubicacin central y mrgenes definidos.').m-auto
    
    .row.justify-content-center.mb-5
      .col-xl-10
        p.mb-0 Este marcado asegura la ubicacin precisa del refuerzo para el herraje, optimizando la resistencia estructural y facilitando el montaje durante la confeccin.
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
