        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn tapa
    
    p.mb-5 La elaboracin del patrn de tapa es un proceso clave para garantizar que el cierre del bolso sea funcional, esttico y se adapte perfectamente a la estructura general. A partir de la base y los fuelles previamente definidos, se realizan mediciones y ajustes que permiten obtener un molde definitivo, listo para guiar el corte y montaje de las piezas.

    .row.align-items-center.mb-5
      .col-xl-6
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.style___box_3.ajuste___box_3
            p.mb-0 Traslado del molde
          .col-lg.style___box_3_1.ajuste___box_3_1
            p.mb-0 Pasa la forma dibujada en el molde patrn a una cartulina, cuidando que los contornos sean precisos.
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.style___box_3.ajuste___box_3
            p.mb-0 Marcado del ancho del fuelle
          .col-lg.style___box_3_1.ajuste___box_3_1
            p.mb-0 Desde la parte superior del patrn, mide y traza una lnea paralela a <strong>8 cm</strong> (ancho del fuelle).
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.style___box_3.ajuste___box_3
            p.mb-0 Generacin del encoque
          .col-lg.style___box_3_1.ajuste___box_3_1
            p.mb-0 Desde la lnea anterior, traza otra lnea paralela a <strong>8 mm</strong> para formar el encoque, que permitir la curvatura y correcto asentamiento de la tapa sobre el bolso.
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.style___box_3.ajuste___box_3
            p.mb-0 Dibujo de la parte posterior
          .col-lg.style___box_3_1.ajuste___box_3_1
            p.mb-0 Proyecta y dibuja la parte posterior de la tapa partiendo desde la ltima lnea trazada, completando as la forma total.
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.style___box_3.ajuste___box_3
            p.mb-0 Marcado de accesorios
          .col-lg.style___box_3_1.ajuste___box_3_1
            p.mb-0 Seala en el molde la ubicacin exacta de las correas y el bolsillo, siguiendo las medidas del trazado inicial para garantizar coherencia en el montaje final.
      .col-xl-6(data-aos="fade-left").espacio___movil_4
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 33.
          span Construccin tapa
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img63.png', alt='Imagen de la construccin de la tapa del bolso, trasladada desde el molde patrn a una cartulina, con lneas para el encoque y ajuste de curvatura sobre el cuerpo del bolso.').m-auto

    p.mb-5.espacio___movil_2 La incorporacin del bolsillo en la tapa permite dividirla en dos secciones a partir de las lneas que delimitan dicho bolsillo. A cada una de estas piezas se le debe aplicar un aumento de 7 mm en todo su contorno, con el fin de facilitar el doblez y asegurar una correcta terminacin durante el proceso de confeccin.

    .row.justify-content-center.mb-5
      .col-xl-6
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 34.
          span Seccin de tapa y aumentos
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img64.png', alt='Ilustracin de la seccin de la tapa dividida en dos partes para incorporacin de bolsillo, con aumentos de costura de 7 mm en todo el contorno.').m-auto
    
    .row.align-items-center.mb-5.m-0
      .col-xl-auto(data-aos="fade-right")
        figure.d-none.d-xl-block
          img(src='@/assets/curso/temas/img65.png', alt='').m-auto
      .col-xl.style___box_12(data-aos="zoom-in")
        p.mb-0 Por ltimo, se dibuja el molde de los terminales de cremallera, los cuales se utilizan para evitar que la cremallera llegue hasta el borde de la tapa, brindando as un mejor acabado. Para su elaboracin, se toma como base el recuadro del bolsillo previamente dibujado en el molde patrn, cuyas dimensiones son de 3 cm por 1.4 cm. A este rectngulo se le agregan aumentos de 1 cm en las lneas horizontales para el armado, y de 7 mm en las lneas verticales para el doblez.
      .col-xl-4.espacio___movil_4
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 35.
          span Terminal cremallera bolsillo tapa
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img66.png', alt='Ilustracin de la seccin de la tapa dividida en dos partes para incorporacin de bolsillo, con aumentos de costura de 7 mm en todo el contorno.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right").espacio___movil_1
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn bolsillo frontal
    
    p.mb-5 Para elaborar este molde, se copia del molde patrn el bolsillo, el cual servir como gua para determinar el recorrido del fuelle. Sin aplicar aumentos, se mide el permetro del bolsillo para establecer el largo del fuelle. El ancho del fuelle depender del diseo; en este caso, ser de 3 cm.

    .row.justify-content-center.mb-5
      .col-xl-10
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 36.
          span Molde frontal y fuelle bolsillo
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img67.png', alt='Ilustracin del molde del bolsillo frontal y su fuelle, representando dimensiones y contornos exactos antes de aplicar aumentos.').m-auto
    
    p.mb-5 Posteriormente, se aplican los aumentos correspondientes: al molde del bolsillo se le da un aumento de 8 mm en el contorno que se unir con el fuelle y 7 mm en la parte superior para el doblez. En el caso del fuelle, se da un aumento de 8 mm en la lnea que lo une con el bolsillo y 7 mm en las partes superiores para el doblez; la parte que se une con el frontal se le da aumento de doblez.

    .row.justify-content-center.mb-5
      .col-xl-10
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 37.
          span Molde frontal y fuelle bolsillo con aumentos
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img68.png', alt='Imagen del molde del bolsillo frontal y su fuelle con aumentos de costura aplicados, listos para el corte en material definitivo.').m-auto
        
        .row.align-items-center.m-0.style___box_13(data-aos="zoom-in").espacio___movil_4
          .col-xl-auto(data-aos="fade-right")
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img69.svg', alt='').m-auto
          .col-xl
            p.mb-0 Recuerde que este bolsillo lleva un hebillero, por lo tanto, su posicin debe quedar debidamente marcada en el molde.
      
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn correa tapa
    
    p.mb-5 El siguiente molde corresponde a la <strong>correa de la tapa</strong>. Para elaborarlo, se toma la forma de la correa directamente del molde patrn y se trasladan sus dimensiones a una cartulina. En todo el contorno se aplica un aumento de <strong>7 mm</strong>, destinado al doblez durante la confeccin. A continuacin, se marcan los puntos de perforacin que permitirn el ajuste con la hebilla, asegurando su correcta ubicacin y funcionalidad:
    
    .row.justify-content-center.mb-5.m-0
      .col-xl-10
        .row.align-items-center.style___box_11.ajuste___box_11_1
          .col-xl-7
            <ul class="fa-ul estilo___ul mb-4">
              <li class="mb-3"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>La primera perforacin se sita a <strong>6,5 cm</strong> del borde inferior de la correa.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>A partir de esta, se trazan dos perforaciones adicionales: una hacia arriba y otra hacia abajo, cada una separada <strong>1,5 cm</strong> de la anterior.</li>
            </ul>
            p.mb-0.style___box_14(data-aos="zoom-in") Este molde se corta en <strong>4 unidades</strong>, ya que el forro o la parte posterior de la correa debe realizarse con el mismo material principal para mantener resistencia y coherencia esttica en el diseo.
          .col-xl-5.espacio___movil_4
            .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
              h5 Figura 38.
              span Patrn correa tapa
            figure(data-aos="zoom-in")
              img(src='@/assets/curso/temas/img70.png', alt='Imagen del molde del bolsillo frontal y su fuelle con aumentos de costura aplicados, listos para el corte en material definitivo.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn hebillero
    
    p.mb-5 El <strong>hebillero</strong> se obtiene a partir del molde patrn, copiando la silueta previamente definida. Esta pieza se traza al doble de su tamao final para permitir su posterior doblado, y se le adicionan aumentos de <strong>7 mm</strong> en los lados ms largos y en la parte inferior, facilitando el remate limpio durante la confeccin.

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center
          .col-xl-6(data-aos="fade-right")
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img71.png', alt='').m-auto
          .col-xl-6
            .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
              h5 Figura 39.
              span Patrn hebillero
            figure(data-aos="zoom-in")
              img(src='@/assets/curso/temas/img72.png', alt='Imagen del patrn del hebillero, pieza rectangular con aumento de doblez en lados largos e inferior, y perforacin central para aguijn de hebilla. ').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right").espacio___movil_2
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Chapeta del fuelle
    
    p.mb-5 La <strong>chapeta del fuelle</strong> es la pieza que, mediante un cuadrante, sostiene la correa larga que permite llevar el bolso al hombro. Para obtener su molde, se toma la figura previamente dibujada sobre el fuelle y se duplica (al doble de su tamao final). A continuacin, se le adicionan aumentos de 7 mm en todo el contorno, excepto en la parte superior, lo que permite un ajuste adecuado durante el montaje y asegura un acabado limpio y resistente en la confeccin.

    .row.justify-content-center.mb-5
      .col-xl-6
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 40.
          span Patrn chapeta
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img73.png', alt='Ilustracin del patrn de chapeta del fuelle, pieza rectangular duplicada con aumentos de costura para su fijacin.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn correa
    
    .row.align-items-center.m-0.mb-5.style___box_7.ajuste___box_7(data-aos="zoom-in")
      .col-xl-auto
        figure.d-none.d-xl-block
          img(src='@/assets/curso/temas/img74.png', alt='').m-auto
      .col-xl
        p Con la chapeta definida, se pasa a la elaboracin del molde de la <strong>correa</strong>, cuyo ancho debe coincidir con el herraje utilizado. En este caso, el herraje es de <strong>1 pulgadas</strong> (aproximadamente 3 cm), por lo que el ancho de la correa ser de <strong>2,8 cm</strong>, igual que el de la chapeta. Al tratarse de una correa graduable, su largo puede variar segn el diseo y la funcionalidad requerida; para este modelo se ha definido un largo de <strong>150 cm</strong>. En todo el contorno se aade un aumento de <strong>7 mm</strong> destinado al doblez durante la confeccin.
        p.mb-0 En muchos casos, la correa se elabora en reata de nailon o algodn, incorporando nicamente terminales en el material principal del bolso para lograr un mejor acabado en las puntas y conservar la coherencia esttica con el resto de la pieza.
    
    .row.align-items-center.mb-5
      .col-xl-4
        h4.mb-3.caja-titulos-cuarto-nivel(data-aos="fade-right") Despiece moldes internos y forros  
        p.mb-0 Tanto los moldes internos como los del forro se obtienen a partir del molde patrn, adaptndolos segn las necesidades del diseo y el tipo de acabado deseado.
      .col-xl-8.espacio___movil_4
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 41.
          span Patrn correa
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img75.png', alt='Imagen del patrn de correa larga ajustable, con medidas para ancho y largo, y aumentos de costura en todo el contorno.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Molde boca y forro frontal
    
    p.mb-5 Para obtener el molde del frontal, se copia directamente del patrn base. En la parte superior, se traza una lnea paralela a 3 cm del borde, dividiendo el molde en dos secciones:

    .row.justify-content-center.mb-5
      .col-xl-10
        .row
          .col-xl-8
            p.mb-2.style___box_15(data-aos="fade-right") Boca, que se corta en el material principal.
            p.mb-5.style___box_15(data-aos="fade-right") Forro, que suele elaborarse en textil o cuero de cerdo, segn el diseo.
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 42.
          span Molde Boca y forro frontal
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img76.png', alt='Ilustracin del molde de la boca y forro frontal del bolso, dividido en dos secciones, con lneas de referencia para unin y aumentos correspondientes.').m-auto
    
    .row.align-items-center.mb-5
      .col-xl-7
        .style___box_12.mb-3(data-aos="fade-right")
          p.mb-3 A continuacin, se aplican los aumentos:
          <ul class="fa-ul estilo___ul mb-0">
            <li class="mb-3"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>En el frontal, <strong>8 mm</strong> en todo el contorno, ya que se unir a los fuelles, la base y la boca.</li>
            <li class="mb-0"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>En el forro, costura a <strong>1 cm</strong> en lugar de 8 mm para que quede ligeramente ms pequeo y facilitar el ensamblaje o enchuspado. Es importante marcar los <strong>ejes de simetra</strong> en este patrn.</li>
          </ul>
        figure.d-none.d-xl-block(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img77.png', alt='').m-auto
      .col-xl-5.espacio___movil_4
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 43.
          span Molde Boca y forro frontal
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img78.png', alt='Ilustracin del molde de la boca y forro frontal del bolso, dividido en dos secciones, con lneas de referencia para unin y aumentos correspondientes.').m-auto
    
    p.mb-4.espacio___movil_5 En este molde tambin se marca la ubicacin del <strong>bolsillo interno y el organizador</strong>. Para el bolsillo interno:
    
    .row.mb-5
      .col-xl-4(data-aos="zoom-in")
        .style___box_16.h-100
          p.mb-0 Traza una lnea paralela a <strong>2 cm</strong> del borde superior.
      .col-xl-4(data-aos="zoom-in").espacio___movil_3
        .style___box_16.h-100
          p.mb-0 Desde esta, otra paralela a <strong>1,4 cm</strong> hacia abajo (canal para cremallera o abertura del bolsillo).
      .col-xl-4(data-aos="zoom-in").espacio___movil_3
        .style___box_16.h-100
          p.mb-0 Desde el eje vertical, mide <strong>10 cm</strong> hacia cada lado para definir el ancho del bolsillo.
    
    .row.justify-content-center.mb-5.m-0
      .col-xl-10
        .row.align-items-center.mb-5.style___box_11.ajuste___box_11_2
          .col-xl-7
            p.mb-4 Una vez dibujado, se obtienen los moldes complementarios:
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-3"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span><strong>Ventana para cremallera</strong>: al rectngulo del bolsillo se le aade <strong>1 cm</strong> en todo el contorno. Se corta en salpa, odena, cartulina o cartn.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span><strong>Forro del bolsillo</strong>: ancho de la boca + <strong>1 cm</strong> a cada lado y altura de <strong>20 cm</strong> (o menor que la distancia entre la lnea inferior del bolsillo y el borde inferior del forro frontal). Se corta al doble para garantizar volumen y funcionalidad.</li>
            </ul>
          .col-xl-5.espacio___movil_4
            .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
              h5 Figura 44.
              span Trazado Bolsillo interno y molde ventana de cremallera
            figure(data-aos="zoom-in")
              img(src='@/assets/curso/temas/img79.png', alt='Ilustracin del trazado del bolsillo interno y el molde de ventana para cremallera, con dimensiones y mrgenes indicados para su confeccin.').m-auto
        
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 45.
          span Forro bolsillo interno
        figure(data-aos="zoom-in").mb-5
          img(src='@/assets/curso/temas/img80.png', alt='Imagen del forro del bolsillo interno, pieza rectangular adaptada a la boca del bolsillo, con medidas para profundidad y mrgenes de ensamblaje.').m-auto
        
        h5.mb-5 Para el organizador, en el molde del forro se dibuja un rectngulo de 22 cm de ancho por 14 cm de alto.

        .row.align-items-center.m-0
          .col-xl-auto(data-aos="fade-right")
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img81.png', alt='').m-auto
          .col-xl.style___box_8.ajuste___box_8_1(data-aos="fade-left")
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-3"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Desde el eje vertical central, se trazan dos lneas paralelas a <strong>11 cm</strong> a cada lado.</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span>Desde la lnea inferior del bolsillo, se marca una paralela hacia abajo a <strong>14 cm</strong>.</li>
            </ul>
    
    .row.align-items-center.mb-5.m-0
      .col-xl.style___box_18(data-aos="zoom-in")
        p.mb-3 Estas lneas definen el rea de ubicacin del organizador. Una vez finalizado el trazado, se borran las lneas de construccin, dejando solo el contorno del rectngulo como gua. Para obtener el molde del organizador, este rectngulo se pasa a cartulina y se le aade un aumento de 3 cm en uno de los lados verticales para dar volumen. Desde esta lnea, se trazan dos paralelas adicionales:
        .row
          .col-xl-6
            p.mb-2.style___box_17(data-aos="fade-right") Una a <strong>8 cm</strong>.
            p.mb-0.style___box_17(data-aos="fade-right") Otra a <strong>3 cm</strong> de la anterior.
      .col-xl-auto(data-aos="fade-left")
        figure.d-none.d-xl-block
          img(src='@/assets/curso/temas/img82.png', alt='').m-auto
    
    p.mb-5 Estas referencias servirn para formar una pinza o fuelle. Luego, se realiza una simetra del molde para obtener la otra mitad y, por ltimo, se aade un aumento de <strong>1 cm</strong> nicamente en la parte superior para facilitar el ensamblaje en la confeccin.

    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 46.
          span Patrn organizador
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img83.png', alt='Ilustracin del patrn del organizador interno, con secciones plegables y medidas especficas para pinzas o fuelles. ').m-auto

    p.mb-4 Por ltimo, para obtener el <strong>patrn de la boca</strong>, se utiliza la franja resultante al seccionar el molde frontal. A esta pieza se le aade, en cada lado, la mitad de la medida del fuelle. Si el fuelle es diagonal, lo ms recomendable es copiar su forma directamente para asegurar un encaje preciso.
    
    p.mb-5 En cuanto a los aumentos, se asignan <strong>7 mm</strong> en el borde superior para el doblez y <strong>8 mm</strong> en los tres lados restantes para la confeccin. En los lados verticales, la costura se realiza a <strong>1 cm</strong>, tal como se indic previamente en el caso del forro frontal.

    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 47.
          span Patrn boca
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img84.png', alt='Imagen del patrn de la boca del bolso, franja rectangular con aumentos diferenciados para doblez y costura, adaptada a la forma del fuelle.').m-auto

    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right").espacio___movil_2
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn forro fuelle base
    
    p.mb-5 Para obtener el <strong>patrn del forro fuelle-base</strong>, se parte del molde del fuelle, descontando <strong>3 cm</strong> correspondientes a la boca. A continuacin, este fuelle se une a la base y se refleja en el otro extremo, formando as una sola pieza continua. En el contorno del molde resultante se otorga un aumento de <strong>8 mm</strong> para la costura. No obstante, en la unin con el forro del frontal, la costura se realiza a <strong>1 cm</strong>, tal como se indic anteriormente, con el fin de mantener la proporcin y facilitar el ensamblaje del forro en el bolso.

    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 48.
          span Patrn forro fuelle base
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img85.png', alt='Ilustracin del patrn de forro del fuelle y base, pieza continua con forma alargada y aumentos aplicados para costura. ').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn contratapa y forro tapa
    
    .row.align-items-center.mb-5.m-0.style___box_1(data-aos="zoom-in")
      .col-xl-auto
        figure.d-none.d-xl-block
          img(src='@/assets/curso/temas/img86.png', alt='').m-auto
      .col-xl
        p El patrn de la contratapa y el forro de la tapa se utiliza para definir las piezas que completan la estructura de la tapa. La contratapa corresponde a la parte posterior y, por lo general, se elabora en el material principal del bolso, mientras que el resto de la tapa suele confeccionarse en textil. En algunos diseos, toda la tapa se forra con carnaza de cerdo para ofrecer un acabado ms fino.
        p.mb-3 Para obtener los moldes, se parte del molde completo de la tapa y se traza una lnea paralela a 6 cm del borde inferior. Esta divisin genera dos secciones:
        .row
          .col-xl-5
            p.mb-2.style___box_17(data-aos="fade-right") Contratapa.
            p.mb-0.style___box_17(data-aos="fade-right") Forro de la tapa.
    
    h5.mb-4 Los aumentos se asignan de la siguiente manera:

    .row.mb-5
      .col-xl-4(data-aos="zoom-in")
        .style___box_16.h-100
          p.mb-0 En la contratapa, <strong>7 mm</strong> en todo el contorno, destinados al doblez.
      .col-xl-4(data-aos="zoom-in").espacio___movil_3
        .style___box_16.h-100
          p.mb-0 En el forro de la tapa, si se utiliza textil, <strong>1 cm</strong> de aumento en todo el contorno. Todos los lados se doblan excepto el borde que se une con la contratapa.
      .col-xl-4(data-aos="zoom-in").espacio___movil_3
        .style___box_16.h-100
          p.mb-0 Una vez cortadas, las piezas se ensamblan y se cosen sobre el forro, manteniendo una terminacin limpia y una correcta adaptacin al diseo del bolso.

    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 49.
          span Moldes contratapa y forro tapa
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img87.png', alt='Imagen de moldes de contratapa y forro de la tapa, con lnea divisoria marcada para diferenciar las dos secciones.').m-auto
    
    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 50.
          span Aumentos contratapa y forro tapa
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img88.png', alt='Ilustracin que muestra los aumentos aplicados a contratapa y forro de tapa, diferenciando doblez y mrgenes de unin.').m-auto

    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn forro bolsillo
    
    p.mb-5 El patrn del forro del bolsillo se obtiene siguiendo el mismo procedimiento utilizado para los moldes elaborados en cuero o en el material principal, con la diferencia de que no se aade el aumento de doblez en el borde que quedar parchado al frontal. Esta adaptacin permite que el forro se ajuste correctamente dentro del bolsillo sin generar exceso de material, garantizando un acabado limpio en la confeccin.

    .row.justify-content-center.mb-5
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 51.
          span Forro frontal y fuelle bolsillo
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img89.png', alt='Imagen del patrn de forro frontal y fuelle del bolsillo externo, con contornos adaptados para un ajuste exacto.').m-auto
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Patrn forro bolsillo tapa
    
    p.mb-3 El <strong>patrn del forro del bolsillo de la tapa</strong> se construye a partir de la distancia entre el borde superior del bolsillo y el borde inferior de la tapa, que en este modelo es de <strong>18 cm</strong>. A esta medida se le descuentan <strong>2 cm</strong> para permitir el doblez inferior, y el resultado se multiplica por <strong>2</strong>, ya que el bolsillo se pliega hacia abajo y luego se dobla nuevamente hacia arriba. De esta forma, se obtiene un largo total de <strong>32 cm</strong>. Para el ancho, se toma como referencia la medida total de la tapa <strong>(40 cm)</strong> y se descuentan <strong>2 cm</strong>, lo que da un ancho final de <strong>38 cm</strong>.

    p.mb-5 En consecuencia, el molde del bolsillo presenta unas dimensiones de <strong>32 cm</strong> de alto por <strong>38 cm</strong> de ancho. Es fundamental trazarlo utilizando <strong>ejes de simetra</strong>, asegurando precisin y equilibrio en el diseo.

    .row.justify-content-center
      .col-xl-8
        .titulo-sexto.color-acento-contenido.mb-2(data-aos="fade-right")
          h5 Figura 52.
          span Forro bolsillo tapa
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/temas/img90.png', alt='Ilustracin del patrn del forro del bolsillo de la tapa, pieza rectangular construida con ejes de simetra para asegurar proporcin.').m-auto

    <!-- BLOQUE 3 -->
    Separador
    #t_5_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 5.3 Despiece de patrones de almas

    p.mb-5 En esta seccin se presentan los moldes internos de refuerzo (almas) utilizados para dar firmeza y estructura al bolso, todos sin aumentos, listos para cortar en el material correspondiente.

    AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")(data-aos="fade-left").mb-5
      .row(titulo="Patrn alma base").ajuste-cajaAcordion
        p.mb-3 Corresponde al molde de la base sin aumentos. 
        .row.justify-content-center.mb-3
          .col-xl-6.mb-3
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 53.
              span Alma base
            figure
              img(src='@/assets/curso/temas/img91.png', alt='Imagen del patrn alma de la base, recorte con misma forma que la base del bolso pero sin mrgenes de costura.').m-auto
      .row(titulo="Patrn alma correas tapa").ajuste-cajaAcordion
        p.mb-3 Corresponde al molde de la correa sin aumentos.
        .row.justify-content-center.mb-3
          .col-xl-4.mb-3
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 54.
              span Alma correas tapa
            figure
              img(src='@/assets/curso/temas/img92.png', alt='Ilustracin del patrn alma de las correas de la tapa, sin mrgenes de costura, con forma alargada y bordes rectos.' style="max-width: 400px")
      .row(titulo="Patrn alma hebillero").ajuste-cajaAcordion
        p.mb-3 Corresponde al molde del hebillero sin aumentos. 
        .row.justify-content-center.mb-3
          .col-xl-6.mb-3
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 55.
              span Alma hebillero
            figure
              img(src='@/assets/curso/temas/img93.png', alt='Imagen del patrn alma del hebillero, pieza rectangular con perforacin central, sin mrgenes de costura.').m-auto
      .row(titulo="Patrn alma chapeta").ajuste-cajaAcordion
        p.mb-3 Corresponde al molde de la chapeta sin aumentos. 
        .row.justify-content-center.mb-3
          .col-xl-4.mb-3
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 56.
              span Alma chapeta
            figure
              img(src='@/assets/curso/temas/img94.png', alt='Ilustracin del patrn alma de chapeta, forma rectangular sin aumentos de costura.' style="max-width: 400px")

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center.m-0.style___box_13(data-aos="zoom-in")
          .col-xl-auto(data-aos="fade-right")
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img95.svg', alt='').m-auto
          .col-xl
            p.mb-0 <strong>Nota:</strong> si el bolso se construye al corte, no se aplican aumentos de doblado, ya que las piezas se ensamblan directamente sin repliegues en los bordes.
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Informacin obligatoria en cada molde
    
    p.mb-5 Para una referencia clara y ordenada, cada molde debe incluir:

    .row.justify-content-center.mb-5
      .col-xl-10
        .row.align-items-center.mb-5.m-0.style___box_1(data-aos="zoom-in")
          .col-xl-auto
            figure.d-none.d-xl-block
              img(src='@/assets/curso/temas/img96.jpg', alt='').m-auto
          .col-xl
            <ul class="fa-ul estilo___ul mb-0">
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span><strong>Referencia</strong>. Cdigo o nmero identificador.</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span><strong>Nombre</strong>. Denominacin de la pieza (ej. Frontal, Base, Correa).</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span><strong>Material</strong>. Tipo de material a utilizar (cuero, textil, acrlico, etc.).</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span><strong>Cantidad</strong>. Nmero de piezas a cortar.</li>
              <li class="mb-1"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span><strong>Dimensiones</strong>. Medidas principales (ancho y largo).</li>
              <li class="mb-0"><span class="fa-li"><i class="fas fa-briefcase color___bag_1"></i></span><strong>Numeracin</strong>. Ejemplo: Molde 1 de [total], Molde 2 de [total], y as sucesivamente.</li>
            </ul>
    
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right").espacio___movil_2
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img6.svg" style="max-width: 60px").m-auto
      .col
        h3 Clasificacin de moldes para este bolso
    
    p.mb-5 Para este modelo, los moldes se agrupan en tres categoras: principales, de forro y de almas.

    .row.align-items-center
      .col-xl-auto(data-aos="fade-right")
        figure.d-none.d-xl-block
          img(src='@/assets/curso/temas/img97.png', alt='').m-auto
      .col-xl
        AcordionA(tipo="a" clase-tarjeta="tarjeta acordeonA")(data-aos="fade-left")
          .row(titulo="Moldes principales").ajuste-cajaAcordion
            <ul class="fa-ul mb-3">
